import React, { useEffect, useState } from "react";
import "./style.scss";
import Swipe from "../Swipe/index";
import Event from "../Event/index";
import Firebase from "../../firebase";

interface FeedProps {
  onClick: any;
  auth: any;
  userData: any;
}

function structureEvents(events: Object | undefined) {
  if (events !== undefined)
    for (const [keya, valuea] of Object.entries(events)) {
      // value a holds the events for that date
      // keya is the date
      for (const [keyb, valueb] of Object.entries(valuea)) {
        // valueb is object with required properties for Event component
        // keyb is the unique key of the event
      }
    }
  let wrapper = <div className="events">{}</div>;
}

export default function Feed({ onClick, auth, userData }: FeedProps) {
  const [events, setEvents] = useState();

  useEffect(() => {
    // pulls events from database
    if (events === undefined)
      Firebase.database
        .ref("events/")
        .once("value")
        .then(function (snapshot) {
          setEvents(snapshot.val());
        });
  });

  return (
    <main>
      <Swipe
        touchEnd={() => {
          onClick(true);
        }}
      ></Swipe>
      <a className="sbbtn" onClick={() => onClick(true)}></a>
      <div className="home" onClick={() => onClick(false)}>
        <h1>This is home</h1>
      </div>
    </main>
  );
}
